import React , {Component} from 'react';
import {Grid, Cell} from 'react-mdl';


class Resume extends Component{
	render(){
		return(
		<div>
			<Grid>
				<Cell col={4}>
					<div style={{textAlign:'center'}}>
						<img
						src="https://i.pinimg.com/564x/46/58/86/46588627efb137ade794d1252aff87f8.jpg"
						alt="avatar"
						style={{height :'200px'}}/>
					</div>
					
					<h2 style={{paddingTop:'2em'}}> Anca-Elena Oprea</h2>
					<h4 style={{color:'grey'}}>Programmer</h4>
					<hr style={{borderTop:'3px solid #833fb2', width:'50%'}}/>
					<p>Als erfahrener Programmierer mit Bachelor-Abschluss im Bereich Digital Automation 
					und internationaler Erfahrung suche ich neue Herausforderungen im Bereich „Digital Intelligence“ sowie die Möglichkeit zur beruflichen
					Weiterentwicklung auf Führungsebene.
					Ich beherrsche alle gängigen Programmiersprachen auf verschiedenen Systemen und bin es gewohnt, 
					eigenverantwortlich nach Lösungen zu suchen und auch größere Projekte zu leiten.</p>
					<hr style={{borderTop:'3px solid #833fb2', width:'50%'}}/>
					<h5>Adresse</h5>
					<p>Grüner Weg 26, 84478 Waldkraiburg </p>
					<h5>Phone</h5>
					<p>(+49)15171279327</p>
					<h5>Email</h5>
					<p>opreaanca1987@gmail.com</p>
					<hr style={{borderTop:'3px solid #833fb2', width:'50%'}} />
					

				</Cell>
				<Cell className="resume-right-col" col={8}>
					<h2>Educatio</h2>

				</Cell>
			
			
			</Grid>
			
			</div>
		)
	}
}

export default Resume;